import Vue from 'vue'
import config from '@/api/default_service.config'
import { requestAboutUs } from '@/api/API'
import BottomBar from '../../components/BottomBar'
import Header from '../../components/Header'
import { Cell } from 'mint-ui'
Vue.component(Cell.name, Cell)

export default {
  name: 'recharge',
  components: {
    Header, BottomBar
  },
  data () {
    return {
      logo: '/static/img/profile/default-user-img.png',
      version: '当前版本：牛牛策略 V1.0.0',
      baseUrl: window.location.host.indexOf('localhost') > -1 ? config.baseURL_TEST : config.baseURL
    }
  },
  mounted () {
    this.getAboutUs()
  },
  methods: {
    async getAboutUs () {
      let res = await requestAboutUs(this.$store.state.User.token)
      if (res.code === 200) {
        this.logo = this.baseUrl + res.data.logo1
        this.version = res.data.copyright
      }
    }
  }
}



